const app = getApp()
var openid=app.globalData.openid;
import serveUrl from '../../common/serveUrl.js'
export default {
	data() {
		return {
			address:[],		//默认地址
			buylist:[],		//订单列表
			goodsPrice:0.0,	//商品合计价格
			sumPrice:0.0,	//用户付款价格
			freight:12.00,	//运费
			note:'',		//备注
			int:1200,		//抵扣积分
			deduction:0,	//抵扣价格
			recinfo:{},
			isShow: true,
			
			userId:'',      //用户id
			addressId:"",	//地址id
			goodsId:'',		//商品id
			openId:'1',      //OpenID
			number:''       //数量
		};
	},
	onLoad(option) {
		this.globalData = app.globalData
		this.userId = this.globalData.userInfo.id
		var url = serveUrl.serverUrl
		var that = this;
		// 默认di是1
		uni.request({
			url: url +'/app/appAddress/queryDefault?userId=1',
			method:"POST",
			success: res => {
				var address = res.data.data;
				that.address = address;
				// console.log(res.data.data[0])
				that.userId = res.data.data[0].userId
				that.addressId = res.data.data[0].id
				// console.log(that.userId)
				// console.log(res.data.data)
				if (this.recinfo.length > 0) {
					this.isShow = false
				}
			}
		})
	},
	onShow() {
		console.log(this.openId)
		//页面显示时，加载订单信息
		uni.getStorage({
			key:'buylist',
			success: (ret) => {
				this.buylist = ret.data;
				console.log('........',ret.data[0])
				this.goodsId = ret.data[0].id
				this.number = ret.data[0].number
				this.goodsPrice=0;
				//合计
				let len = this.buylist.length;
				for(let i=0;i<len;i++){
					this.goodsPrice = this.goodsPrice + (this.buylist[i].number*this.buylist[i].price);
				}
				this.deduction = this.int/100;
				this.sumPrice = this.goodsPrice-this.deduction+this.freight;
			}
		});
		uni.getStorage({
			key:'selectAddress',
			success: (e) => {
				this.recinfo = e.data;
				uni.removeStorage({
					key:'selectAddress'
				})
			}
		})
	},
	onHide() {
		
	},
	onBackPress() {
		//页面后退时候，清除订单信息
		this.clearOrder();
	},
	filters: {
		toFixed:function(x) {
			return parseFloat(x).toFixed(2);
		}
	},
	methods: {
		// 获取备注信息
		write(e){
			var value = e.detail.value;
			this.value = this.note;
			
		},
		clearOrder(){
			uni.removeStorage({
				key: 'buylist',
				success: (res)=>{
					this.buylist = [];
					console.log('remove buylist success');
				}
			});
		},
		toPay(){
			//商品列表
			let paymentOrder = [];
			let goodsid=[];
			let len = this.buylist.length;
			for(let i=0;i<len;i++){
				paymentOrder.push(this.buylist[i]);
				goodsid.push(this.buylist[i].id);
			}
			if(paymentOrder.length==0){
				uni.showToast({title:'订单信息有误，请重新购买',icon:'none'});
				return ;
			}
			//本地模拟订单提交UI效果
			uni.showLoading({
				title:'正在提交订单...'
			})
			setTimeout(()=>{
				uni.setStorage({
					key:'paymentOrder',
					data:paymentOrder,
					success: () => {
						
						uni.hideLoading();
						uni.redirectTo({
							url:"/pages/money/pay?userId="+this.userId+"&addressId="+this.addressId+"&goodsId="+this.goodsId+
					"&openid="+this.openId+"&num="+this.number+"&userMsg="+this.note+"&amount="+this.sumPrice
						})
					}
				})
			},1000)
			
		},
		//选择收货地址
		selectAddress(){
			uni.navigateTo({
				url:'/pages/address/address?type=select'
			})
		}
	}
}
